import Vue from 'vue'
import Router from 'vue-router'

const Home = () => import('../views/Home.vue')
const Register = () => import('../views/RegisterPage.vue')
const Login = () => import('../views/LoginPage.vue')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      redirect: '/home',
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
    },
    {
      name: 'register',
      path: '/register',
      component: Register,
    },
    {
      name: 'login',
      path: '/login',
      component: Login,
    },
  ],
  mode: 'history'
})



