import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({

  state: {
    // 存储token
    Authorization: localStorage.getItem('Authorization') ? localStorage.getItem('Authorization') : '',

    //存储当前需要展示的notebook：
    currentNotebook: {},

    lock_flag:false,

    label_list:{}, // 存放label_node表
    height_list:[], // 存放每行代码的高度

    blush_flag:false,
    blush_st:null, // 一次刷选的起点
    blush_ed:null, // 一次刷选的终点
    raw_list:[], // 一次刷选前的checked状态

    comment_st:-1,
    comment_ed:-1,

    newing_flag:false,
    commenting_index:-1,
    comment_list:[],
    comment_sort:[],
  },

  mutations: {
    // 修改token，并将token存入localStorage
    changeLogin (state, user) {
      state.Authorization = user.Authorization
      localStorage.setItem('Authorization', user.Authorization)
    },

    //检查邮箱合法性
    checkEmail(state, value) {
      let reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
      if (value === ''){
        alert("邮箱必填哦")
      }
      else if(!reg.test(value)) {
        alert("检查邮箱是否填错~")
      }
      else{
        console.log("邮箱合理~")
      }
    },

    //获取当前要展示的notebook信息
    getCurrentNotebook(state, data) {
      state.currentNotebook = data
    },


    setLockFlag(state){
      state.lock_flag = true;
    },
    cleanLockFlag(state){
      state.lock_flag = false;
    },
    setNewingFlag(state){
      state.newing_flag = true;
    },
    cleanNewingFlag(state){
      state.newing_flag = false;
    },
    setSelectingFlag(state){
      state.selecting_flag = true;
    },
    cleanSelectingFlag(state){
      state.selecting_flag = false;
    },

    initLabelList(state,label_list){
      state.label_list = label_list
    },
    initHeightList(state,height_list){
      state.height_list = height_list
    },
    initRawList(state,raw_list){
      state.raw_list = raw_list
    },
    setBlushFlag(state,blush_flag)
    {
      state.blush_flag = blush_flag
    },
    setBlushSt(state,blush_st)
    {
      state.blush_st = blush_st
    },
    setBlushEd(state,blush_ed)
    {
      state.blush_ed = blush_ed
    },

    checkComment(state)
    {
      let flag = true;
      state.comment_st = -1
      state.comment_ed = -1
      for(let i=0;i<state.label_list.length;i++)
      {
        //console.log("i:",i,state.label_list[i].firstChild.checked,flag,state.comment_st,state.comment_ed)
        if(state.label_list[i].firstChild.checked)
        {
          if(state.comment_st===-1)
          {
            state.comment_st = i;
          }
          else if(state.comment_ed!==-1)
          {
            flag = false;
            break
          }
        }
        else
        {
          if(state.comment_st!==-1&&state.comment_ed===-1)
          {
            state.comment_ed = i;
          }
        }
      }
      if(flag&&state.comment_st!==-1&&state.comment_ed===-1)
      {
        this.comment_ed = this.label_list.length;
      }
      if(flag&&state.comment_st!==-1)
      {
        state.comment_ed = state.comment_ed-1;
      }
      else
      {
        state.comment_st = -1
        state.comment_ed = -1
      }
    },
    cleanChecked(state)
    {
      for(let i=0;i<state.label_list.length;i++)
      {
        state.label_list[i].firstChild.checked = false;
      }
    },
    setChecked(state,comment_index)
    {
      for(let i=0;i<state.comment_list[comment_index].st_id;i++)
      {
        state.label_list[i].firstChild.checked = false;
      }
      for(let i=state.comment_list[comment_index].st_id;i<=state.comment_list[comment_index].ed_id;i++)
      {
        state.label_list[i].firstChild.checked = true;
      }
      for(let i=state.comment_list[comment_index].ed_id+1;i<state.label_list.length;i++)
      {
        state.label_list[i].firstChild.checked = false;
      }
    },
    cleanCommentingColor(state)
    {
      for(let i=0;i<state.label_list.length;i++)
      {
        state.label_list[i].className = state.label_list[i].className.replace(" nb-commenting-label","");
      }
    },
    setCommentingColor(state)
    {
      for(let i=0;i<state.label_list.length;i++)
      {
        state.label_list[i].className = state.label_list[i].className.replace(" nb-commenting-label","");
        //console.log(this.label_list[i].id,this.comment_list[this.commenting_index].st_id,this.comment_list[this.commenting_index].ed_id)
        if(state.label_list[i].firstChild.checked)
        {
          state.label_list[i].className = state.label_list[i].className + " nb-commenting-label";
        }
      }
    },
    cleanCommentedColor(state,comment_index)
    {
      for(let i=state.comment_list[comment_index].st_id;i<=state.comment_list[comment_index].ed_id;i++)
      {
        state.label_list[i].className = state.label_list[i].className.replace(" nb-commented-label","");
      }
    },
    setCommentedColor(state,comment_index)
    {
      //console.log(comment_index,state.comment_list[comment_index])
      for(let i=state.comment_list[comment_index].st_id;i<=state.comment_list[comment_index].ed_id;i++)
      {
        state.label_list[i].className = state.label_list[i].className + " nb-commented-label";
        //console.log(i,state.label_list[i].className)
      }
    },


    setCommentingIndex(state,comment_index){
      state.commenting_index = comment_index;
    },
    cleanCommentingIndex(state){
      state.commenting_index = -1;
    },
    adjustCommentTop(state,{comment_index,new_top}){
      state.comment_list[comment_index].cur_top = new_top
      //console.log('EEEEEEE',comment_index,new_top,state.comment_list)
    },
    insertComment(state,{c_id,u_id,nb_id,st_id,ed_id,cur_top,time_str,content_str}){
      state.comment_list.push({
        c_id:c_id,
        u_id:u_id,
        nb_id:nb_id,
        st_id:st_id,
        ed_id:ed_id,
        cur_top:cur_top,
        time_str:time_str,
        content_str:content_str
      })
    },
    updateComment(state,{comment_index,content_str,st_id,ed_id}){
      state.comment_list[comment_index].content_str = content_str
      state.comment_list[comment_index].st_id = st_id
      state.comment_list[comment_index].ed_id = ed_id
    },
    insertCommentSort(state,{comment_index,rank})
    {
      state.comment_sort.splice(rank,0,comment_index)
    },
    deleteComment(state,comment_index)
    {
      state.comment_list.splice(comment_index,1)
      let comment_rank = -1;
      state.comment_sort = state.comment_sort.map((idx,rank)=>{
        if(idx>comment_index)
        {
          idx -= 1;
        }
        else if(idx===comment_index)
        {
          comment_rank = rank
        }
        return idx;
      })
      state.comment_sort.splice(comment_rank,1)
      //console.log(comment_index,comment_rank,state.comment_list,state.comment_sort)
    }
  }
})

export default store



